.stars {
  display: flex;

  &__star {
    display: flex;

    background-image: url("../images/star.svg");
    background-position: center;
    background-repeat: no-repeat;

    height: 16px;
    width: 16px;

    margin-right: 4px;
  }

  @for $i from 1 through 5 {
    &--#{$i} &__star:nth-child(-n + #{$i}) {
      background-image: url("../images/star-active.svg");
    }
  }
}
